import React, { Component } from "react";
import styled from "styled-components";
import { debounce } from "lodash";
import HeroPhoto from "../../assets/images/hero-image.png";
import Button from "../Button/Button";
import linesPattern from "../../assets/images/lines-pattern.svg";
import IconSVG from "../IconSVG/IconSVG";
import logoMain from "../../assets/images/logo-main.svg";

const StyledWrapper = styled.header`
  position: relative;
  height: 95vh;
  width: 100vw;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: -10;
  ::after {
    z-index: -10;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-image: url(${HeroPhoto});
    background-size: cover;
    background-position: 50% 0%;
    background-repeat: no-repeat;
    transform: ${({ ratio, showAbout }) =>
      ratio && showAbout ? `scale(${ratio})` : "scale(1.3) "};
    opacity: ${({ showAbout }) => (showAbout ? "1" : "0")};
    transition: transform 0.55s ease-out, opacity 0.35s linear;

    @media only screen and (min-width: 768px) and (orientation: portrait) {
      background-position: 45% 0;
    }
    @media only screen and (min-width: 375px) and (min-height: 812px) and (orientation: portrait) {
      background-position: 53% 0;
    }
    @media only screen and (min-width: 1024px) and (min-height: 769px) and (orientation: landscape) {
      background-position: 65% 0;
    }
  }

  ::before {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
  }

  /* DESKTOP */

  .lines {
    position: absolute;
    bottom: -2%;
    left: 0;
    margin: 0;
    opacity: 0;
    transition: opacity 0.15s ease-in-out;
    will-change: opacity;

    @media only screen and (min-width: 640px) and (orientation: landscape) {
      opacity: 1;
    }

    svg {
      width: 180px;

      @media only screen and (min-width: 730px) and (orientation: landscape) {
        width: 220px;
      }
      @media only screen and (min-width: 1024px) and (orientation: landscape) {
        width: 400px;
      }
    }
  }
`;

const Wrapper = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 70%;
  height: 70%;
  margin-top: 55px;
  align-items: center;
  justify-content: center;
  z-index: 1;
  flex-direction: column;

  .logo {
    width: 270px;
    transform: ${({ showAbout }) =>
      showAbout ? "translate(-10%, 0)" : "translate(-10%, -70%)"};
    opacity: ${({ showAbout }) => (showAbout ? "1" : "0")};
    transition: transform 0.75s 0.25s ease-out, opacity 0.35s 0.35s linear;

    .e,
    .f,
    .g,
    .h,
    .i,
    .j,
    .k,
    .l,
    .m,
    .n,
    .o {
      opacity: 0;
      transition: opacity 0.2s ease-in-out;

      @media only screen and (min-width: 568px) and (orientation: landscape) {
        opacity: 1;
      }
      @media only screen and (min-width: 768px) {
        opacity: 1;
      }
    }

    @media only screen and (min-width: 375px) and (min-height: 812px) and (orientation: portrait) {
      width: 310px;
    }
    @media only screen and (min-width: 731px) {
      width: 300px;
    }
    @media only screen and (min-width: 736px) and (orientation: portrait) {
      width: 410px;
    }
    @media only screen and (min-width: 1024px) {
      width: 520px;
    }
    @media only screen and (min-width: 1024px) and (orientation: portrait) {
      width: 570px;
    }
  }

  @media only screen and (min-width: 480px) and (orientation: landscape) {
    width: 60%;
  }
  @media only screen and (min-width: 736px) {
    margin-left: 30px;
  }
  @media only screen and (min-width: 1024px) {
    width: 70%;
  }

  @media only screen and (min-width: 375px) and (min-height: 812px) and (orientation: portrait) {
    transform: translate(-5%, -5%);
  }

  @media only screen and (min-width: 1024px) {
    transform: translate(-5%, 0);
  }
`;

class Header extends Component {
  state = {
    ratio: 1
  };

  //////////////////IMPORTANT SCROLL

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);

    setTimeout(() => {
      this.props.handleAboutMount();
    }, 3500);
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = debounce(() => {
    if (window.scrollY <= window.innerHeight) {
      this.setState({
        ratio: (100 + window.scrollY / 200) / 100
      });
    } else return;
  }, 50);
  //////////////////IMPORTANT SCROLL

  render() {
    return (
      <StyledWrapper ratio={this.state.ratio} showAbout={this.props.showAbout}>
        <Wrapper showAbout={this.props.showAbout}>
          <IconSVG
            className={"logo"}
            src={logoMain}
            showAbout={this.props.showAbout}
          />
          <Button content={"umów się na wizytę"} />
        </Wrapper>
        <IconSVG className={"lines"} src={linesPattern} />
      </StyledWrapper>
    );
  }
}

export default Header;
