import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import HomeTemplate from "./HomeTemplate/HomeTemplate";
import AboutTemplate from "./AboutTemplate/AboutTemplate";
import IconSVG from "../components/IconSVG/IconSVG";
import { Loader } from "../components/Loader/Loader";
import Logo from "../assets/images/logo-main-white.svg";
import ContactTemplate from "./ContactTemplate/ContactTemplate";
import TemplateLoader from "../components/TemplateLoader/TemplateLoader";
import BurgerMenu from "../components/BurgerMenu/BurgerMenu";
import Burger from "../components/Burger/Burger";
import SocialMenu from "../components/SocialMenu/SocialMenu";
import styled from "styled-components";

const StyledUpperWrapper = styled.div`
  position: absolute;
  margin-top: 10px;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;

  div:nth-child(1) {
    display: flex;
  }
`;

const RouterWrapper = styled.div`
  overflow: hidden;
  width: 100vw;
`;

class MainTemplate extends Component {
  state = {
    isLoaded: false,
    isScrollable: false,
    isOpen: false,
    id: 0,
    showAbout: false
  };

  handleAboutMount = () => {
    this.setState({
      showAbout: true
    });
  };

  handleButton = () => {
    this.setState(prevState => ({
      isOpen: !prevState.isOpen,
      id: prevState.id + 1,
      isScrollable: !prevState.isScrollable
    }));
  };

  setLoader = e => {
    if (e.target.className === "active") {
      this.setState({
        isOpen: false
      });
    } else {
      window.scrollTo(0, 0);
      const loader = document.querySelector(".template-loader div");
      loader.classList.add("active");

      this.setState({
        isScrollable: false,
        showAbout: false
      });

      setTimeout(() => {
        this.setState({
          isOpen: false
        });
      }, 1000);

      setTimeout(() => {
        loader.classList.remove("active");
        this.setState({
          isScrollable: true,
          showAbout: true
        });
      }, 2000);

      setTimeout(() => {
        this.setState({
          showAbout: true
        });
      }, 1700);
    }
  };

  componentDidMount() {
    setTimeout(() => {
      window.scrollTo(0, 0);
      this.setState({
        isLoaded: true
      });
    }, 500);

    setTimeout(() => {
      this.setState({
        isScrollable: true
      });
    }, 3500);
  }

  componentWillUnmount() {
    this.setState({
      isLoaded: false
    });
  }

  render() {
    return (
      <>
        <Loader isLoaded={this.state.isLoaded}>
          <IconSVG className={"logo"} src={Logo} />
        </Loader>
        <TemplateLoader className={"template-loader"} />

        <RouterWrapper>
          <Router basename={process.env.PUBLIC_URL}>
            <BurgerMenu isOpen={this.state.isOpen} setLoader={this.setLoader} />
            <StyledUpperWrapper className={"upperWrapper"}>
              <div>
                <SocialMenu />
              </div>
              <div>
                <Burger
                  handleButton={this.handleButton}
                  isOpen={this.state.isOpen}
                  id={this.state.id}
                />
              </div>
            </StyledUpperWrapper>
            <Switch>
              <Route
                path="/"
                exact
                render={props => (
                  <HomeTemplate
                    {...props}
                    isScrollable={this.state.isScrollable}
                    showAbout={this.state.showAbout}
                    handleAboutMount={this.handleAboutMount}
                  />
                )}
              />
              <Route
                path="/about"
                exact
                render={props => (
                  <AboutTemplate
                    {...props}
                    isScrollable={this.state.isScrollable}
                    showAbout={this.state.showAbout}
                    handleAboutMount={this.handleAboutMount}
                  />
                )}
              />
              <Route
                path="/contact"
                exact
                render={props => (
                  <ContactTemplate
                    {...props}
                    isScrollable={this.state.isScrollable}
                  />
                )}
              />
            </Switch>
          </Router>
        </RouterWrapper>
      </>
    );
  }
}

export default MainTemplate;
